import React from "react";

import image1 from '../../assets/images/image-1.png';
import image2 from '../../assets/images/image-2.png';
import image3 from '../../assets/images/image-3.png';
import './Home.css';

const HomePage = () => {
  return (
    <>
      <section className="headline">
        <h1 className="headline-text-primary">React</h1>
        <p className="text">A JavaScript library for building user interfaces</p>
      </section>
      <section className="features">
        <div className="feature-container">
          <img
            src={image1}
            alt="Declarative"
          />
          <h2 className="features-header">Declarative</h2>
          <p className="text">
          React makes it painless to create interactive UIs. Design simple views for each state in your application, and React will efficiently update and render just the right components when your data changes.
          </p>
        </div>
        <div className="feature-container">
          <img
            src={image3}
            alt="Flexbox Feature"
          />
          <h2 className="features-header">Learn Once, Write Anywhere</h2>
          <p className="text">
          We don’t make assumptions about the rest of your technology stack, so you can develop new features in React without rewriting existing code.
          </p>
        </div>
        <div className="feature-container">
          <img
            src={image2}
            alt="Flexbox Feature"
          />
          <h2 className="features-header">Component-Based</h2>
          <p className="text">
            Build encapsulated components that manage their own state, then compose them to make complex UIs.
          </p>
        </div>
      </section>
    </>
  );
};

export default HomePage;
