import React from "react";
import styled from "styled-components";

const Footer = styled.div`
    padding: 30px;
    text-align: center;
    background-color: #f1f1f1;
`;

const Text = styled.p`
    color: #171717;
    font-size: 16px;
    margin-top: 10px;
`;

const Link = styled.a`
    color: #555;
    text-decoration: none;
    border-bottom: 2px solid currentColor;
`;

const FooterApp = () => {
  return (
    <Footer>
        <Text>
          <span role="img" aria-label="Author of the website">👨🏻‍💻</span>
          <Link href="https://www.pirmansyah.id">Pirmansyah</Link> | 
          <span role="img" aria-label="Source code">📝</span> 
          <Link href="https://github.com/pirmanshah">Github</Link>
        </Text>
    </Footer>
  );
};

export default FooterApp;
